"use strict";
/**
 * @file
 * 贪食蛇游戏
 */
Object.defineProperty(exports, "__esModule", { value: true });
const Snake_1 = require("./Snake");
const Ground_1 = require("./Ground");
const Direction_1 = require("./Direction");
const Food_1 = require("./Food");
const Print_1 = require("./Print");
class Game {
    constructor({ width = 10, height = 10, headerPosition, direction, bodyLength = 3, }) {
        /**
         * 食物数组
         * @private
         * @type {Array<IFood>}
         * @memberof Game
         */
        this.$food = [];
        //初始化方向
        if (direction) {
            direction = Direction_1.Direction.Up;
        }
        //设置头部的默认值，方向默认值
        if (!headerPosition) {
            headerPosition = {
                x: Math.floor(width / 2),
                y: Math.floor(height / 2),
            };
        }
        //创建蛇
        this.$snake = new Snake_1.Snake({
            headerPosition: headerPosition,
            direction: direction,
            bodyLength: bodyLength,
        });
        //创建贪食蛇的坐标系
        this.$ground = new Ground_1.Ground(width, height);
        //创建绘制对象
        this.$print = new Print_1.Print(width, height);
        //创建食物
        this.$createFood(3);
    }
    /**
     * 一帧
     */
    frame() {
        var eatFood = this.$snake.move(this.$food);
        if (eatFood) {
            //去除已经吃掉的食物
            this.$food = this.$food.filter(item => item != eatFood);
            this.$createFood();
        }
        this.$print.rest();
        this.$print.printHead(this.$snake.header, this.$snake.direction);
        this.$print.printBody(this.$snake.header, this.$snake.body);
        this.$print.printFoot(this.$food);
        return this.$print.cells;
    }
    /**
     * 转向
     * @param willTurnDirection     方向
     */
    turn(willTurnDirection) {
        this.$snake.turn(willTurnDirection);
    }
    /**
     * 检验游戏是否可以继续
     */
    check() {
        let snakeCell = [...this.$snake.body, this.$snake.header];
        let snakeCellMap = {};
        return {
            result: [...this.$snake.body, this.$snake.header].every(cell => {
                if (snakeCellMap[cell.x + ':' + cell.y] || !this.$ground.inGround(cell)) {
                    return false;
                }
                else {
                    snakeCellMap[cell.x + ':' + cell.y] = true;
                    return true;
                }
            })
        };
    }
    /**
     * 创建食物
     */
    $createFood(count = 1) {
        const cells = this.$ground.getRandomUsableCells(count, this.$food, this.$snake.header, this.$snake.body);
        cells.forEach(cell => {
            this.$food.push(new Food_1.Food(cell.x, cell.y, Math.floor(Math.random() * 3) + 1));
        });
    }
}
exports.Game = Game;



